import { Form, Placeholder } from "react-bootstrap";

const CustomInput = ({ type, Placeholder, className, onChange, id, value, rows = 1 }) => {

    return (
        <Form.Group controlId={id}>
            {
                type != "textarea" ?
                    <Form.Control
                        type={type}
                        placeholder={Placeholder}
                        className={className}
                        onChange={onChange}
                        value={value}
                    /> :
                    <Form.Control as="textarea" rows={rows} className={className} onChange={onChange} value={value} />
            }
        </Form.Group>
    );
};

export default CustomInput;