import React, { useEffect, useMemo, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import DragAndDropComponent from "../../pages/DocumentCreation/DragAndDrop";
import { Image } from "react-bootstrap";
import "./DocumentCreationWidget.css";
import Loader from "../../components/Loader/Loader";
import ReusableTable from "../../components/Table/Table";
import CardWithDragAndDrop from "../../components/Card/cardwithdraganddrop";
import {
  setDocumentCreationHideDetailPage,
  setDroppedObjectData,
  setIsDropped,
  setPlantObjectData,
} from "../../store/droppedObjectSlice";
import { getCardData, getTableData, tableColumns } from "./DocumentCreationHelpers";
import useToast from "../../hooks/useToast";
import { saveData } from "../../services/api/PlantAssignment/saveTableDataService";
import { MSG_SAVE_FAILURE, MSG_SAVE_SUCCESS } from "../../utils/toastMessages";
import useBOSDropableArea from "../../hooks/useBOSDropableArea";
import DocumentCreationToolbarCta from "../DocumentCreation/DocumentCreationWidgetToolbarCta";
import { callEnoviaWebService, callEnoviaWebServiceFileUpload, fetchData } from "../../utils/helpers";
import { fetchCsrfToken } from "../../services/api/PlantAssignment/fetchCsrfService";
import useDocumentCreationDropableArea from "../../hooks/useDocumentCreationDropableArea";

const DocumentCreationWidget = () => {
  const { initializeDroppableArea, loading } = useDocumentCreationDropableArea();
  const [tableKey, setTableKey] = useState(0);
  const [tableData, setTableData] = useState([]);
  const [isTableLoading, setIsTableLoading] = useState(false);
  const [screenLoader, setScreenLoader] = useState(false);
  const [isCardDataAvailable, setIsCardDataAvailable] = useState(false);
  const [specData, setSpecData] = useState([]);
  const [selectableRows, setSelectableRows] = useState([]);
  const dispatch = useDispatch();
  const { showSuccessToast, showErrorToast } = useToast();

  const handleSave = async (type, fileInfo, comments) => {
    setScreenLoader(true);
    const fileName = fileInfo != null ? fileInfo.name : "";

    const formattedData = {
      "data": [
        {
          "id": droppedObjectData.cardData['Dropped Revision ID'],
          "type": type,
          "dataelements": {
            "name": (cardData?.EIN != "N/A" && cardData?.EIN != "" && cardData?.EIN != undefined) ? cardData?.EIN : cardData?.title != "N/A" ? cardData?.title : "",
            "policy": "Document Release",
            "state": "IN_WORK",
            "title": (cardData?.EIN != "N/A" && cardData?.EIN != "" && cardData?.EIN != undefined) ? cardData?.EIN : cardData?.title != "N/A" ? cardData?.title : "",
            "description": cardData?.Description !== "N/A" ? cardData?.Description : "",
            "comments": comments ? comments : "",
            "collabspace": "Micro Motion",
            "parentRelName": "SpecificationDocument",
            "parentDirection": "from",
            "extensions": [
              "IPML.Automatic"
            ],
          },
          "relateddata": {
            "parents": [
              {
                "id": droppedObjectData.cardData['Dropped Revision ID'],
                "type": "VPMRefrence",
                "updateAction": "CONNECT",
                "dataelements": {
                  "parentRelName": "SpecificationDocument"
                },
              }
            ]
          }
        }
      ]
    }

    const headers = await fetchCsrfToken();
    console.log("Formatted Data:", formattedData, headers);
    const saveURL =
      "https://oi000186152-us1-space.3dexperience.3ds.com/enovia/resources/v1/modeler/documents";
    let response;
    await callEnoviaWebService("POST", saveURL, formattedData, headers).then((res) => {
      response = res;
      return response;
    }).catch((error) => {
      console.log("Error in catch", error);
      response = error;
      return response;
    });
    if (response.status === true && response.output.statusCode === 200) {
      console.log("response.output.data", response.output.data);
      if (fileInfo != null) {
        let id = response.output.data[0].id;
        let checkinTicketApiResponse;
        const apiURL = `https://oi000186152-us1-space.3dexperience.3ds.com/enovia/resources/v1/modeler/documents/${id}/files/CheckinTicket`;
        await callEnoviaWebService("PUT", apiURL, formattedData, headers).then(async (CheckinTicketRes) => {
          checkinTicketApiResponse = CheckinTicketRes;
          if (CheckinTicketRes.status === true && CheckinTicketRes.output.statusCode === 200) {
            // let csrfData = await fetchCsrfToken();
            // let csrfData = CheckinTicketRes.output.csrf;
            let ticketData = CheckinTicketRes.output.data[0].dataelements;
            let checkinURLApiResponse;
            // const checkinURL = `https://stg001us1-dfcs.3dexperience.3ds.com/fcs/servlet/fcs/checkin`;
            const checkinURL = ticketData?.ticketURL;
            await callEnoviaWebServiceFileUpload("POST", checkinURL, ticketData, fileInfo, headers).then(async (checkinRes) => {
              checkinURLApiResponse = checkinRes;
              console.log("checkinURLApiResponse", checkinURLApiResponse);
              let responseString = checkinURLApiResponse?.output;
              const modelerDocumentsURL = `https://oi000186152-us1-space.3dexperience.3ds.com/enovia/resources/v1/modeler/documents/`;
              let modelerDocumentPayload = {
                "data": [
                  {
                    "relateddata": {
                      "files": [
                        {
                          "dataelements": {
                            "title": fileName,
                            "receipt": responseString
                          },
                          "updateAction": "CREATE",
                        },
                      ]
                    },
                    // "id": droppedObjectData.cardData['Dropped Revision ID'],
                    "id": response.output.data[0].id,
                    "updateAction": "NONE"
                  }
                ]
              }
              let newheaders = { ...headers, "ENO_CSRF_TOKEN": CheckinTicketRes.output.csrf?.value };
              await callEnoviaWebService("PUT", modelerDocumentsURL, modelerDocumentPayload, newheaders).then((modelerDocumentRes) => {
                if (modelerDocumentRes?.output?.statusCode === 200) {
                  setScreenLoader(false);
                  showSuccessToast(MSG_SAVE_SUCCESS);
                } else {
                  showErrorToast("Something went wrong");
                  setScreenLoader(false);
                }
                //  return checkinURLApiResponse;
              }).catch((error) => {
                console.log("Error in checkinURLApiResponse", error);
                response = error;
                showErrorToast("Something went wrong");
                setScreenLoader(false);
                return response;
              });
            }).catch((error) => {
              console.log("Error in checkinURLApiResponse", error);
              response = error;
              showErrorToast("Something went wrong while file uploading");
              setScreenLoader(false);
              return response;
            });
          }
          return checkinTicketApiResponse;
        }).catch((error) => {
          console.log("Error in catch", error);
          response = error;
          showErrorToast("Something went wrong while file uploading");
          setScreenLoader(false);
          return response;
        });
      } else {
        showSuccessToast(MSG_SAVE_SUCCESS);
      }

      setTableData(response.output.data);
      // setSpecData(tableData);
      setScreenLoader(false);
    } else {
      showErrorToast(response?.errorMessage);
      setScreenLoader(false);
    }
  };

  const selectedTableRows = useSelector(
    (state) => state.droppedObject.selectedTableRows
  );
  // Access Redux store
  const droppedObjectData = useSelector(
    (state) => state.droppedObject.droppedObjectData
  );
  const loadingParentDetails = useSelector(
    (state) => state.droppedObject.loadingParentDetails
  );
  const proposedChanges = useSelector(
    (state) => state.droppedObject.plantObjectData.proposedChanges
  );

  const isDropped = useSelector((state) => state.droppedObject.isDropped);
  const bosData = useSelector((state) => state.droppedObject.BOSObjectData);

  const bosSpecDocument = useSelector(
    (state) => state.droppedObject.BOSObjectData.specDocument
  );

  const documentCreationData = useSelector(
    (state) => state.droppedObject.documentCreationData?.data
  );

  const dropObjectDetailsData = useSelector(
    (state) => state.droppedObject.documentCreationData?.dropObjectDetails
  );

  const showDetailPageDocumentCreation = useSelector(
    (state) => state.droppedObject.documentCreationData?.showDetailPage
  );

  useEffect(() => {
    if (tableData.length >= 0) {
      setTableKey((prevKey) => prevKey + 1); // Increment the key to trigger a re-render
    }
  }, [tableData]); // Runs whenever tableData changes
  if (droppedObjectData.cardData && droppedObjectData.initialDraggedData) {
    var state = droppedObjectData.cardData["Maturity State"];
    var type = droppedObjectData.initialDraggedData?.data?.items[0].objectType;
  }

  useEffect(() => {
    if (!isDropped) {
      initializeDroppableArea();
    }
  }, [isDropped, initializeDroppableArea]);
  useEffect(() => {
    setIsTableLoading(loadingParentDetails);
  }, [loadingParentDetails]);

  const cardData = useMemo(
    () => getCardData(droppedObjectData),
    [droppedObjectData]
  );
  useEffect(() => {
    setIsCardDataAvailable(!!cardData);
  }, [cardData]);
  const columns = useMemo(
    () =>
      tableColumns(
        type,
        droppedObjectData?.cardData["Latest Revision"],
        droppedObjectData?.cardData["Dropped Revision"]
      ),
    [type, droppedObjectData?.cardData]
  );

  const handleHomeClick = () => {
    initializeDroppableArea(); // Reset the droppable area
    dispatch(setIsDropped(false));
    dispatch(
      setDroppedObjectData({
        cardData: {},
        parentDetails: [],
        versions: [],
        initialDraggedData: [],
      })
    ); // Clear Redux state
    dispatch(
      setPlantObjectData({
        allPlants: [],
        initialAssignedPlants: [],
        uniquePlants: [],
        productChildren: [],
        CAName: false,
        headers: {},
      })
    );
    setTableData([]); // Clear local table data
    setIsCardDataAvailable(false);
  };
  const optimizedTableData = useMemo(() =>
    getTableData(tableData, cardData), [tableData]
  );

  useEffect(() => {
    //filter based on the conditions
    const filteredData = documentCreationData.filter((item) => {
      const itemTitle = item?.dataelements?.title?.trim();
      const objEIN = dropObjectDetailsData?.EIN?.trim();
      const objTitle = dropObjectDetailsData?.Title?.trim();

      if (objEIN) {
        return itemTitle === objEIN;
      } else {
        return itemTitle === objTitle;
      }
    });

    setTableData(filteredData);
  }, [documentCreationData])

  return (
    <>

      {screenLoader && (
        <div className="loading-overlay">
          <Loader />
          <p>Saving...</p>
        </div>
      )}
      {!isDropped && !loading && !isTableLoading && <DragAndDropComponent />}
      {loading && <Loader />}
      {isDropped && (
        <>
          {loading && <Loader />}
          <div className="content-wrapper py-3 border-bottom">
            <div className="d-flex ">
              <div className=" p-0 pt-4">
                <Image
                  src="https://thewhitechamaleon.github.io/testrapp/images/home.png"
                  alt="home-icon"
                  className="home-icon"
                  onClick={handleHomeClick}
                />
              </div>
              {cardData && (
                <CardWithDragAndDrop data={cardData} widgetType="documentCreationWidget" />
              )}
            </div>
          </div>

          {isTableLoading ? (
            <div className="loading-indicator mt-5">
              <Loader />
            </div>
          ) : (
            <>
              <div className="wrapper-cta">
                {
                  showDetailPageDocumentCreation === false ?
                    <>
                      <DocumentCreationToolbarCta
                        onSave={handleSave}
                        type={type}
                        latestRevision={
                          droppedObjectData?.cardData["Latest Revision"]
                        }
                        droppedRevision={
                          droppedObjectData?.cardData["Dropped Revision"]
                        }
                        selectedRows={selectedTableRows}
                        state={state}
                        tableData={optimizedTableData}
                        documentCreationData={documentCreationData}
                        cardData={cardData}
                      />
                      <ReusableTable
                        showActionButtons={false}
                        key={tableKey}
                        data={optimizedTableData}
                        columns={columns}
                        type={type}
                        widgetType="Doc_Creation_Widget"
                        onSelectableRowsChange={setSelectableRows}
                      />
                    </> :
                    <div>
                      API Data
                      <button className="btn btn-primary" onClick={() => { dispatch(setDocumentCreationHideDetailPage()); }}>Close</button>
                    </div>
                }
              </div>
            </>
          )}
        </>
      )}
    </>
  );
};
export default DocumentCreationWidget;
