import { useState } from "react";
import Popup from "../../components/Popup/Popup";
import useToast from "../../hooks/useToast";
import { MSG_BOS_SAVE } from "../../utils/toastMessages";
import DocumentCreationModal from "../../components/Modals/DocumentCreationModal";
import CustomButton from "../../components/Button/Button";

const DocumentCreationToolbarCta = ({
  onSave,
  state,
  selectedRows,
  tableData,
  type,
  latestRevision,
  droppedRevision,
  documentCreationData,
}) => {
  const { showWarningToast } = useToast();
  const [showDropdown, setShowDropdown] = useState(false);
  const [isShowPopup, setIsShowPopup] = useState(false);

  const handaleShowPopup = () => {
    setIsShowPopup(true);
  };
  const handleClosePopup = () => {
    setIsShowPopup(false);
  };

  return (
    <div className="d-flex flex-column">
      <div className="d-flex">
        {
          tableData.length > 0 ? <></> :
            <CustomButton 
            text={"+ New Document"}
            variant="outline"
              className={`btn btn-outline-primary btn-lg m-2 ${tableData.length > 0 ? 'disabled' : ''}`}
              disabled={tableData.length > 0 ? true : false}
              onClick={() => {
                handaleShowPopup();
              }
              }
            />
            }
      </div>
      <DocumentCreationModal
        show={isShowPopup}
        onHide={handleClosePopup}
        onSave={onSave}
      />
      {/* Dropdown appears below */}
      {/* {showDropdown && (
      )} */}
    </div>
  );
};

export default DocumentCreationToolbarCta;
